import { pointsGrid } from "../utils/point";
import { rgbRandomPastelColor } from "../utils/tools";

import * as util from "canvas-sketch-util/random";

import chroma from "chroma-js";
import p5 from "p5";
const canvasSketch = require("canvas-sketch");

const p5_ = new p5();

const settings = {
	p5: true,
	dimensions: [2048, 2048],
	animate: true,
	fps: 24,
	playbackRate: "throttle",
	attributes: { antialias: true },
};

const sketch = ({ context, width, height }) => {
	const points = pointsGrid(width, height, width / 8, 8, 100);
	let noiseXOff = random();
	let noiseYOff = random();
	const c = [rgbRandomPastelColor(), rgbRandomPastelColor()];
	const cN = 5;
	const offset = 0.00001;
	const palette = chroma.scale(c).mode("lch").colors(cN);
	points.forEach((el) => {
		el.setColor(palette[parseInt(util.range(0, cN))]);
	});
	const l = 100;
	return ({ context, width, height }) => {
		background(255);
		strokeWeight(15);
		points.forEach((el) => {
			const [x, y] = el.getCoordinates();
			const c = el.getColor();
			const angle = noise(x * offset + noiseXOff, y * offset + noiseYOff);
			p5_.stroke(color(c));
			p5_.push();
			p5_.translate(x, y);
			p5_.rotate(angle * 180);
			p5_.rect(0 - l / 2, 0, l, 3);
			p5_.pop();
			noiseXOff += offset;
			noiseYOff += offset;
		});
	};
};

canvasSketch(sketch, settings);
