export class Point {
	constructor(x, y, r = 1, color = "rgb(0,0,0)") {
		this.x = x;
		this.y = y;
		this.r = r;
		this.color = color;
		this.dx = 0;
		this.dy = 0;
	}

	draw(context) {
		context.save();
		context.fillStyle = this.color;
		context.beginPath();
		context.arc(this.x, this.y, this.r, 0, Math.PI * 2, false);
		context.fill();
		context.restore();
	}

	move(width, height) {
		if (this.x + this.dx > width || this.x + this.dx < 0) {
			this.dx *= -1;
		}
		if (this.y + this.dy > height || this.y + this.dy < 0) {
			this.dy *= -1;
		}
		this.x = this.x + this.dx;
		this.y = this.y + this.dy;
	}

	setVector(dx, dy) {
		this.dx = dx;
		this.dy = dy;
	}

	setRadius(r) {
		this.r = r;
	}
	getRadius() {
		return this.r;
	}

	setColor(color) {
		this.color = color;
	}

	getColor() {
		return this.color;
	}

	getCoordinates() {
		return [this.x, this.y];
	}
}

export function pointsGrid(width, height, border, n, r) {
	const lineX = width - 2 * border;
	const lineY = height - 2 * border;
	const stepX = lineX / (n - 1);
	const stepY = lineY / (n - 1);
	const points = [];
	for (let i = 0; i < n; i++) {
		const x = border + stepX * i;
		for (let j = 0; j < n; j++) {
			const y = border + stepY * j;
			points.push(new Point(x, y, r));
		}
	}
	return points;
}
